.navbar
  width: clamp(204px, 16.5vw, 297px)
  height: 100%
  padding: 20px 0 20px 10px
  box-sizing: border-box
  @media (max-width: 1000px)
    flex:  0 1 0px
    padding: 0
    height: auto
.navbar_noLogin
  @media (max-width: 1000px)
    display: none

.container 
  position: fixed
  z-index: 2
  width: clamp(204px, 16.5vw, 297px)
  @media (max-width: 1000px)
    background: #fff
    width: 100%
    display: flex
    align-items: flex-end
    justify-content: center
    gap: 35px
    bottom: 0
    height: 65px
    box-shadow: 4px 0px 26px 0px rgba(180, 187, 215, 0.15)

.item
  width: 100%
  color: #1A1A1A
  font-family: Inter, sans-serif
  font-size: 20px
  font-style: normal
  font-weight: 500
  line-height: 24px
  text-align: left
  padding: 10px
  background-color: #fff
  border: none
  cursor: pointer
  &:not(:last-child) 
    margin: 0 0 5px
  @media (max-width: 1000px)
    padding: 0 0 6px
    width: auto
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
  @media (max-width: 768px)
    font-size: 14px

.itemActive
  border-radius: 12px
  background: rgba(180, 187, 215, 0.10)
  @media (max-width: 1000px)
    border-radius: 0px
    background: none
    color: #550FDA
    & .item_template * 
      fill: #550FDA

.item_template 
  display: none
  @media (max-width: 1000px)
    display: block
